import React from "react";
import {connect} from 'react-redux';

import {increment,reset} from '../store/votes';

const Counter = (props)=>{

  return(
    <section>
      <ul>
        {props.counter.candidates.map(candidate=>{
        return  (<li onClick={()=>props.increment(candidate.name)} key={candidate.name}> 
             {candidate.name}:{candidate.counter}
            </li>)
        })}
       
      </ul>
    </section>
  )

}

const mapStateToProps = state =>({
  counter: state.reducers
})

const mapDispatchToProps = {increment,reset} ;

export default connect(mapStateToProps,mapDispatchToProps)(Counter)

